<script scpped>
import { getHSIssuedList, getAllOrgList ,mdhs} from "@/api/info";

export default {
  name: "questionBank",
  data() {
    return {
      radio:'1',
       pageSizes: 10,
       pageNum:1,
       total: 0,
       currentPage3: 1,
       BorgId:'',//机构
       orgId: "", //客户信息
       state:'',//状态
       reTsrCode:'',//员工代码
       tsrName:'',//员工姓名
       position:'',//角色
       batchId:'',//批次号
       batchName:'',//批次名称
       codeName:'',//名单来源
       songCodeName:'',//子名单来源
       cusCode:'',//客户代码
       cusName:'',//客户姓名
       genger:'',//客户性别
       ageStart:'',//开始年龄
       ageEnd:'',//结束年龄
       tsrCode:'',
       statusList:[
        {
          value:'1',
          lable:'在职'  
        },
         {
          value:'0',
          lable:'离职'
        }
      ],//人员状态
      orgListValue: "", //角色
      roleList: [],
      orgList: [],
      activeName: "first",
      status:'',//员工状态
      sex:'',//性别
      fAge:'',//年龄开始
      eAge:'',//年龄结束
      sexualitys:[
        {
          lable:'女',
          value:'2' 
        },
         {
          lable:'男',
          value:'1'
        }
      ],

      tableData: [],
      delateDigFlag: false, //删除列表提示弹框
      questionNum: "一",
      multipleSelection: [], //选择框选中列表
      currentPage4: 4,
      delateDigFlagSure: false,
      user:'{}',//登录人员参数
      infoObj:'',
      type:'1',//客户列表，员工列表
      departId:'',
      departCode:'',
    };
  },
  created(){
    let userStorage = JSON.parse(sessionStorage.getItem('userInfo')); 
    this.user = userStorage
    this.tsrCode = userStorage.tsrCode;
    this.orgId = userStorage.orgId
    this.departId = userStorage.departId;
    this.departCode = userStorage.departCode;
  },
  mounted() {
    //查询角色/机构列表
    this.getAllOrgList();
    //查询默认数据
    this.getHSIssuedLists();
  },
  methods: {
    changeValue(){
     // console.log(this.position)
    },
    //查询角色/机构列表
    getAllOrgList() {
      let param = {
        parentId: this.departId
      };
      getAllOrgList(param).then(res => {
        let data = res;
        this.roleList = data && data.roleList;
        this.orgList = data && data.orgList;
      }).catch(err =>{
       this.$message.error('操作失败');
     });
    },
    //查询默认数据
    getHSIssuedLists() {
     // console.log(this.pageSizes)
      let param = {
        type:this.type + '',
        pageSize: this.pageSizes,
        pageNum:this.pageNum,
        curOrgId:this.orgId,
        tsrCode:"",
        orgId: this.BorgId, //机构
        state:this.state,//状态
        reTsrCode:this.reTsrCode,//员工代码
        tsrName:this.tsrName,//员工姓名
        position:this.position,//角色
        batchId:this.batchId,//批次号
        batchName:this.batchName,//批次名称
        codeName:this.codeName,//名单来源
        songCodeName:this.songCodeName,//子名单来源
        cusCode:this.cusCode,//客户代码
      //  cusName:this.cusName,//客户姓名
        genger:this.genger,//客户性别
        ageStart:this.ageStart,//开始年龄
        ageEnd:this.ageEnd,//结束年龄
      };
      getHSIssuedList(param).then(res => {
        let data = res && res.dataList;
        let dataCount = res && res.dataCount;
        this.tableData = data;
        this.total= parseInt(dataCount) 
      }).catch(err =>{
      /*  this.$message.error('操作失败'); */
     });
    },
    //确认名单回收
    listRecovery(){
       this.delateDigFlag = false;
       let tsrCodeParam = {
        orgId: this.orgId, //机构
        state:this.state,//状态
        reTsrCode:this.reTsrCode,//员工代码
        tsrName:this.tsrName,//员工姓名
        position:this.position,//角色
        batchId:this.batchId,//批次号
        batchName:this.batchName,//批次名称
        codeName:this.codeName,//名单来源
        songCodeName:this.songCodeName,//子名单来源
        cusCode:this.cusCode,//客户代码
        cusName:this.cusName,//客户姓名
        genger:this.genger,//客户性别
        ageStart:this.ageStart,//开始年龄
        ageEnd:this.ageEnd,//结束年龄
      };
      let param = {};
       if(this.type == 1){
           param = {
                tsrCodeList:this.multipleSelection,
                type:this.type + '',
                user:this.user,
           }
     
       }else{
            param = {
                tsrCodeList:this.multipleSelection,
                type:this.type + '',
                tsrCodeParam:tsrCodeParam,
                user:this.user,
            }
       }
     
    //回收列表
     mdhs(param).then(res =>{
        if(res.code == '000'){
            this.delateDigFlag = false;
            this.delateDigFlagSure = true
             this.getHSIssuedLists();
        }
     }).catch(err =>{
       this.$message.error('操作失败');
     })
    },
    open(){
      if(this.multipleSelection.length > 0){
        this.delateDigFlag = true
      }else{
         this.$message('请勾选名单列表');
      }
      
    },
    //勾选回收列表
    handleSelectionChange(val) {
       let mapBatchId =[];
        for( let i = 0; i < val.length; i++){
          let obj = {};
          if(this.type == 1){
            obj.isDistribute = val[i].isDistribute;
            obj.customerId = val[i].customerId;
            obj.orgId = val[i].orgId;
            mapBatchId.push(obj)
          }else{
            obj.orgId = val[i].orgId;
            mapBatchId.push(obj)
          }
         
        }
      this.multipleSelection = mapBatchId;
    },
    handleSizeChange(val) {
   // console.log(val)
      
    },
    handleCurrentChange(val) {
    //  console.log(val)
       this.pageNum = val;
      this.getHSIssuedLists();
    },
    handleClick(tab, event) {
      this.type = parseInt(tab.index) + 1;
      this.getHSIssuedLists();
    },
  } //methods
};
</script>

